* {
  box-sizing: border-box;
}

body {
  font-family: Arial, Helvetica, sans-serif;
}
ul {
  display: flex;
}
ul li {
  margin: 0 1em;
}

/* Style the counter cards */
.card {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2); /* this adds the "card" effect */
  /* padding: 16px; */
  position: relative;
}
.horizontal {
  display: flex;
  background-color: #fff;
  border-radius: 0 3px 3px 0;
}
.card-image {
  height: 290px;
}

.card-image img {
  width: 200px;
  height: 290px;
}
.card-content {
  padding: 1rem;
}

.card-content p,
.card-content .header {
  color: #666;
}

.card-content .header {
  padding-bottom: 10px;
}
/* Responsive columns - one column layout (vertical) on small screens */
@media screen and (max-width: 600px) {
  .column {
    width: 100%;
    display: block;
    margin-bottom: 20px;
  }
}

/* Text Styles*/

.Education {
  color: #3a3a3a;
}
.Workplace {
  color: #3a3a3a;
}
.PersonalInfo {
  color: #3a3a3a;
}
p {
  color: rgb(10, 10, 10);
  /* text-align: center; */
}

/* Hide the count on the slider, and the card header icon */
.card-slide-item .card .card-body .card-body-total,
.card-slide-item .card .card-header .card-header-icon {
  display: none;
}

.card-slide {
  /* background: #fff; */
  width: 100%;
  overflow: hidden;
  border-radius: 4px;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.05), 0 0 0 1px rgba(63, 63, 68, 0.1);
  background-color: unset !important;
  margin-bottom: 30px;
}

.card-slide-item .card .card-header .card-header-title {
  margin: 0 !important;
}

* {
  box-sizing: border-box;
