/* Text Styles*/

.Education {
  color: #3a3a3a;
}
.Workplace {
  color: #3a3a3a;
}
.PersonalInfo {
  color: #3a3a3a;
}
p {
  color: rgb(10, 10, 10);
  /* text-align: center; */
}

/* Hide the count on the slider, and the card header icon */
.card-slide-item .card .card-body .card-body-total,
.card-slide-item .card .card-header .card-header-icon {
  display: none;
}

.card-slide {
  /* background: #fff; */
  width: 100%;
  overflow: hidden;
  border-radius: 4px;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.05), 0 0 0 1px rgba(63, 63, 68, 0.1);
  background-color: unset !important;
  margin-bottom: 30px;
}

.card-slide-item .card .card-header .card-header-title {
  margin: 0 !important;
}
